import { request } from 'utils';

const prefix = '/api/education';

export const createClassSubjectConcatBySubject = (data) =>
  request({
    prefix,
    url: `/ClassSubjectConcat/CreateClassSubjectConcatBySubject`,
    data: {
      subjectId: data.subjectId,
      classIds: data.classIds,
    },
    method: 'POST',
  });


