import React, { Component } from 'react';
import {
  Card,
  Row,
  Col,
  message,
  Spin,
} from 'antd';
import Page from 'components/LayoutComponents/Page';
import { CartHeadAction } from 'components/common';
import { getQueryParams } from 'utils/urlUtil';
import {
  getPlanSubjectDetail,
  createUpdatePlanSubjectDetails,
  getTeacherInPlanSubjectDetail,
  exportPlanSubjectDetailExcel,
  exportPlanSubjectDetailPdf,
} from 'services/planSubjectDetailâ€‹';
import {
  createClassSubjectConcatBySubject,
} from 'services/classSubjectConcat';
import { makeid } from 'utils/functionUtil';
import {
  getListFieldsNeedValidate,
  areFieldsValidate
} from 'utils/formUtil';

import SearchBarTrainingPlan from './FormTrainingPlan';
import MenuTabTop from './MenuTabTop';
import TrainingPlanTableDetail from './TrainingPlanTableDetail';
import './style.scss';

class TrainingPlanTable extends Component {
  constructor(props) {
    super(props);
    this.refForm = React.createRef();
    this.refFormRaw = React.createRef();
    this.state = {
      loading: false,
      reload: 0,
      dataTraningPlan: null,
      selectedRows: null,
      rowKey: "autoKey",
    };
  }

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = async () => {
    const { loading } = this.state;
    if (loading) {
      return;
    }
    try {
      const paramSearch = getQueryParams();
      if (
        !!paramSearch.trainTypeId &&
        !!paramSearch.facultyId &&
        !!paramSearch.courseId &&
        !!paramSearch.studyFieldId &&
        !!paramSearch.yearEduId &&
        !!paramSearch.semesterId
      ) {
        this.setState({
          loading: true,
        });
        const response = await getPlanSubjectDetail(paramSearch);
        let tempData = [...response.data.items];
        tempData = tempData.map(item => {
          item.autoKey = `${makeid(8)}_${item.id}`;
          return item;
        })
        this.setState({
          dataTraningPlan: [...tempData],
        });
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  handleSelectedRows = (selectedRows) => {
    this.setState({
      selectedRows,
    });
  }

  handleSave = async () => {
    try {
      const { selectedRows, rowKey } = this.state;
      const currentTableData = this.refFormRaw.getTableData();
      if (selectedRows && selectedRows.length) {
        const listFieldName = [
          'studyPeriodTypeId',
          'splitPeriod',
          'studySessionId',
          'teacherId',
          'roomId',
          'fromWeek',
          'toWeek',
        ];
        const listSelectedKey = selectedRows.map(item => {
          return item[rowKey];
        });
        const listSelectedItem = currentTableData.filter(item => listSelectedKey.includes(item[rowKey]));
        const listFieldsNeedValidate = getListFieldsNeedValidate(listSelectedItem, rowKey, listFieldName);
        this.refForm.current.clearField();
        await this.refForm.current.validateFields(listFieldsNeedValidate, async (err) => {
          if (err) {
            return;
          }
          if (areFieldsValidate(listSelectedItem, listFieldName) === false) {
            message.success('CÃ³ dá»¯ liá»‡u á»Ÿ trang khÃ¡c chÆ°a Ä‘Æ°á»£c Ä‘iá»n Ä‘á»§!');
            return;
          }
          this.setState({
            loading: true,
          });
          const formatDataToSave = {
            planSubjectDetails: [...listSelectedItem],
          }
          await createUpdatePlanSubjectDetails(formatDataToSave);
          this.setState({
            dataTraningPlan: null,
            selectedRows: null,
            loading: false,
          },
            () => this.handleChangeQueryParams(),
          );
          message.success('LÆ°u thÃ nh cÃ´ng');
        });
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  handleAuto = async () => {
    try {
      const { rowKey } = this.state;
      const currentTableData = this.refFormRaw.getTableData();
      const listFieldName = [
        'studySessionId',
        'fromWeek',
        'toWeek',
      ];
      const listSelectedKey = currentTableData.map(item => {
        return item[rowKey];
      });
      const listSelectedItem = currentTableData.filter(item => listSelectedKey.includes(item[rowKey]));
      const listFieldsNeedValidate = getListFieldsNeedValidate(listSelectedItem, rowKey, listFieldName);
      await this.refForm.current.validateFields(listFieldsNeedValidate, async (err) => {
        if (err) {
          return;
        }
        if (areFieldsValidate(listSelectedItem, listFieldName) === false) {
          message.success('CÃ³ dá»¯ liá»‡u á»Ÿ trang khÃ¡c chÆ°a Ä‘Æ°á»£c Ä‘iá»n Ä‘á»§!');
          return;
        }
        this.setState({
          loading: true,
          selectedRows: null,
        });
        const formatDataToSave = {
          planSubjectDetails: [...listSelectedItem],
        }
        const response = await getTeacherInPlanSubjectDetail(formatDataToSave);
        let tempData = [...response.data.items];
        tempData = tempData.map(item => {
          item.autoKey = `${makeid(8)}_${item.id}`;
          return item;
        })
        this.setState({
          dataTraningPlan: [...tempData],
        });
        message.success('PhÃ¢n bá»• tá»± Ä‘á»™ng thÃ nh cÃ´ng');
      })
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  handleMerge = async () => {
    try {
      const { selectedRows, rowKey } = this.state;
      const currentTableData = this.refFormRaw.getTableData();
      if (selectedRows && selectedRows.length) {
        const listFieldName = [
          'studyPeriodTypeId',
          'splitPeriod',
          'studySessionId',
          'teacherId',
          'roomId',
          'fromWeek',
          'toWeek',
        ];
        const listSelectedKey = selectedRows.map(item => {
          return item[rowKey];
        });
        const listSelectedItem = currentTableData.filter(item => listSelectedKey.includes(item[rowKey]));
        const listFieldsNeedValidate = getListFieldsNeedValidate(listSelectedItem, rowKey, listFieldName);
        this.refForm.current.clearField();
        this.setState({
          loading: true,
        });
        await this.refForm.current.validateFields(listFieldsNeedValidate, async (err) => {
          if (err) {
            return;
          }
          if (areFieldsValidate(listSelectedItem, listFieldName) === false) {
            message.success('CÃ³ dá»¯ liá»‡u á»Ÿ trang khÃ¡c chÆ°a Ä‘Æ°á»£c Ä‘iá»n Ä‘á»§!');
            return;
          }

          const listClassId = listSelectedItem.map(item => {
            return item.classId;
          });
          const formatDataToSave = {
            subjectId: listSelectedItem[0].subjectId,
            classIds: listClassId,
          }
          await createClassSubjectConcatBySubject(formatDataToSave);
          this.setState({
            dataTraningPlan: null,
            selectedRows: null,
            loading: false,
          },
            () => this.handleChangeQueryParams(),
          );
          message.success('GhÃ©p lá»›p thÃ nh cÃ´ng');
        })
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  isSelectedRowsValid = () => {
    let isValid = false;
    const { selectedRows } = this.state;
    if (selectedRows && selectedRows.length && selectedRows.length >= 2) {
      isValid = true;
      const firstConcatTempCheck = `${selectedRows[0].subjectId}-${selectedRows[0].studyPeriodTypeId}`;
      for (let index = 0; index < selectedRows.length; index += 1) {
        const element = selectedRows[index];
        const tempConcatTempCheck = `${element.subjectId}-${element.studyPeriodTypeId}`;
        if (firstConcatTempCheck !== tempConcatTempCheck) {
          isValid = false;
          break;
        }
      }
    }
    return isValid;
  }

  handleExportPDF = async () => {
    try {
      this.setState({
        loading: true,
      });
      const paramSearch = getQueryParams();
      await exportPlanSubjectDetailPdf(paramSearch);

    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  handleExportExcel = async () => {
    try {
      this.setState({
        loading: true,
      });
      const paramSearch = getQueryParams();
      await exportPlanSubjectDetailExcel(paramSearch);
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  render() {
    const { dataTraningPlan, reload, rowKey, loading, selectedRows } = this.state;
    const isDisabledSave = !(selectedRows && selectedRows.length);
    const isDisabledAuto = !(dataTraningPlan && dataTraningPlan.length);
    const isDisabledMerge = this.isSelectedRowsValid() === false;
    return (
      <Spin spinning={loading}>
        <Card title="PhÃ¢n bá»• giÃ¡o viÃªn, ca há»c, phÃ²ng há»c">
          <Row>
            <Col span={24}>
              <CartHeadAction>
                <MenuTabTop
                  onExportExcel={this.handleExportExcel}
                  onExportPDF={this.handleExportPDF}
                />
              </CartHeadAction>
            </Col>
            <Col span={24}>
              <SearchBarTrainingPlan
                onSave={this.handleSave}
                onAuto={this.handleAuto}
                onMerge={this.handleMerge}
                isDisabledSave={isDisabledSave}
                isDisabledAuto={isDisabledAuto}
                isDisabledMerge={isDisabledMerge}
              />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <TrainingPlanTableDetail
                reload={reload}
                data={dataTraningPlan}
                ref={this.refForm}
                handleSelectedRows={this.handleSelectedRows}
                rowKey={rowKey}
                wrappedComponentRef={(form) => {
                  this.refFormRaw = form;
                }}
              />
            </Col>
          </Row>
        </Card>
      </Spin>
    );
  }
}

export default props => (
  <Page {...props}>
    <TrainingPlanTable />
  </Page>
);



